.jumbotron-billboard .img {
   margin-bottom: 0px;
   opacity: 0.2;
   color: #fff;
   background: #000 url('./assets/white-painted-wall.jpg') top center no-repeat;
   width: 100%;
   height: 100%;
   background-size: auto;
   overflow: hidden;
   position: absolute;
   top: 0;
   left: 0;
   z-index: 1;
}

.jumbotron h2 {
   width: fit-content;
   margin: 0 15%;
   font-size: 40px;
   font-family: 'Manrope';
   font-weight: 500;
}

.jumbotron {
   position: relative;
   padding-top: 50px;
   padding-bottom: 50px;
}

@media screen and (max-width: 500px) {
   .jumbotron h2 {
      margin: 0 auto;
   }
}